import { getSearch } from '@/utils'
/**
 * @Date: 2021-1-12 11:23:49
 * @author: zw
 * @description: 回传相关参数
 * @param {*} phone
 * @param {*} message
 */
export function postBack () {
  const { protocol, host, hash, search, pathname } = location
  return {
    ...getSearch(), // get参数,协议,域名,链接等参数用于回传
    protocol,
    host,
    link: pathname + search + hash
  }
}
