<template>
  <div class="page-teacherQrCode">
    <BindPhone></BindPhone>
    <!-- 底部内容 -->
    <div class="static-box mt__50">
      <img
        class="static-img"
        :src="require('@/assets/TeacherQrCode/payOver.png')" />
      <div class="ml__10">已付款</div>
    </div>
    <div class="mt__20 git-box">
      <img :src="require('@/assets/TeacherQrCode/down.gif')" />
    </div>
    <div class="content-title mt__20">请务必扫码添加, 否则无法上课</div>
    <!-- 二维码 -->
    <div class="qrCode-box">
      <img :src="teacherCodeImg" ref="qrcode" />
    </div>
    <div class="qrCode-text">长按识别二维码, 添加老师微信</div>
    <div class="qrCode-topic">*老师会在24小时内通过好友申请, 不会影响上课</div>
  </div>
</template>

<script>
import BindPhone from './components/BindPhone'
import wxAuth from '@/helper/Auth/wxAuth'
import { postBack } from '@/helper/payConfig'
import { mapState } from 'vuex'
import { getSearch } from '@/utils'
export default {
  components: { BindPhone },
  data () {
    return {
      teacherCodeImg: '' // 老师二维码
    }
  },
  async beforeRouteEnter (to, from, next) {
    await wxAuth({ getTokenQuery: postBack() })
    next()
  },
  computed: {
    ...mapState('user', ['userInfo'])
  },
  mounted () {
    this.$log.addLongEvent(this.$refs.qrcode, e => {
      const search = getSearch()
      this.$log.long({
        current_page: location.href,
        module_ori: '老师二维模块',
        module_part: '106_toufang_lcx_0205'
      })
      this.$apis.userOperator.pressClassQr(search)
    })
  },
  async created () {
    wx.ready(() => {
      wx.hideAllNonBaseMenuItem()
    })
    this.getCOde()
  },
  methods: {
    getCOde () {
      let num = 4
      this.getQRcode()
      this.timer = setInterval(async () => {
        // 获取二维码
        num--
        if (num <= 0) {
          clearInterval(this.timer)
        }
        this.getQRcode()
      }, 1000)
    },
    async getQRcode () {
      const phone = this.userInfo.mobile
      const productSn = this.$route.query.productSn || 'PRODUCT_SN_XIAOBAI_0'
      const result = await this.$apis.baiduPcPayOrder.getClassQr({
        product_sn: productSn,
        phone
      })
      this.teacherCodeImg = result.qr_url
    }
  }
}
</script>

<style lang="scss">
.page-teacherQrCode {
  // 付款盒子
  .static-box {
    @include horVerCenter;
    .static-img {
      width: 49px;
      height: 53px;
    }
    div {
      font-size: 36px;
      font-weight: 400;
      color: #3a3a52;
    }
  }
  .git-box {
    @include horVerCenter;
    img {
      width: 35px;
      height: 35px;
    }
  }
  // 内容标题
  .content-title {
    @include horVerCenter;
    font-size: 36px;
    font-weight: 500;
    color: #f62724;
  }
  .qrCode-box {
    margin: 30px auto;
    width: 30vw;
    height: 30vw;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .qrCode-text {
    text-align: center;
    font-size: 26px;
    font-weight: 400;
    color: #787878;
  }
  .qrCode-topic {
    margin: 52px auto;
    width: calc(100% - 150px);
    height: 50px;
    background: #ffeaea;
    opacity: 0.8;
    border-radius: 25px;
    font-size: 24px;
    font-weight: 400;
    color: #f62724;
    @include horVerCenter;
  }
}
</style>
