<template>
  <!-- 绑定手机号盒子 -->
  <div class="phone-box">
    <!-- 未绑定 -->
    <template v-if="!hasBindPhone">
      <div class="ml__90 mt__30">
        <div class="phone-title">您当前还未绑定手机号</div>
        <div class="phone-desc">绑定手机号用于接收课程信息</div>
      </div>
      <div class="ml__40 mt__30 btn__bind" @click="isBinding = true">
        立即绑定
      </div>
    </template>
    <!-- 绑定 -->
    <VanPopup
      v-model="isBinding"
      closeable
      close-icon="close"
      position="top"
      round
      class="pb__40"
      :style="{ height: 'auto' }">
      <div class="bind-title">请输入手机号以便接收课程信息</div>
      <div class="popup-item mt__30">
        <div class="label">
          +86
          <img :src="require('@/assets/TeacherQrCode/phone-icon.png')" alt="" />
        </div>
        <input v-model.trim="phone" placeholder="请输入手机号" />
      </div>
      <div class="popup-item__code pl__150">
        <input v-model.trim="code" placeholder="请输入验证码" />
        <CountDownBtn
          class="phone-code"
          :phone="phone"
          :get-code-success-back="getCodeSuccessBack"></CountDownBtn>
      </div>
      <div class="bind-topic mt__40">* 如长时间收不到验证码 *</div>
      <div class="bind-topic mt__10">
        可点击右上角关闭，添加老师微信后再绑定
      </div>
      <div class="bind-btn" @click="submitBind">提交</div>
    </VanPopup>
    <!-- 绑定成功 -->
    <template v-if="hasBindPhone">
      <div class="bind__phone phone-title">您当前绑定手机号为：{{ phone }}</div>
    </template>
  </div>
</template>
<script>
import { Toast, Dialog } from 'vant'
import Store from '@/store'
import { clickLog } from '@/helper/clickLog'
import CountDownBtn from '@/components/CountDownBtn'
import wxAuth from '@/helper/Auth/wxAuth'
import { postBack } from '@/helper/payConfig'
import { isPhone } from '@/utils'
import { mapActions } from 'vuex'

export default {
  name: 'BindPhone',
  components: { CountDownBtn },
  data () {
    return {
      phone: '', // 手机号
      code: '', // 验证码
      isBinding: false, // 是否显示绑定弹窗
      hasBindPhone: false // 是否显示绑定弹窗
    }
  },
  async created () {
    // 获取微信授权
    await wxAuth({ getTokenQuery: postBack() })
    await this.getUserInfo()
    const { mobile } = Store.state.user.userInfo || {}
    this.phone = mobile
    this.hasBindPhone = !!mobile
    this.isBinding = !mobile
  },
  methods: {
    ...mapActions('user', ['getUserInfo']),
    /**
     * @Date: 2021-01-05 15:56:58
     * @author: zjs
     * @description: 绑定手机号
     */
    async submitBind () {
      clickLog({ module_ori: '表单模块', module_part: '106_toufang_lcx_0134' })
      const { phone, code } = this
      if (!isPhone(phone)) {
        Dialog({ message: '手机号码有误，请重填' })
        return
      }
      if (!code) {
        Dialog({ message: '请输入手机号验证码' })
        return
      }
      await this.$apis.baiduPcPayOrder.bindPhone({
        phone,
        code
      })
      this.isBinding = false
      this.hasBindPhone = true
      Toast('绑定成功')
    },
    /**
     * @Date: 2021-01-05 15:56:58
     * @author: zjs
     * @description: 手机验证码成功回调
     */
    getCodeSuccessBack () {
      clickLog({ module_ori: '表单模块', module_part: '106_toufang_lcx_0133' })
    }
  }
}
</script>
<style lang="scss">
// 电话盒子
.phone-box {
  width: 100%;
  padding-bottom: 30px;
  background: #ffffff;
  box-shadow: 0px 4px 30px 0px rgba(58, 58, 82, 0.4);
  display: flex;
  justify-content: flex-start;
  .bind-title {
    margin-top: 55px;
    font-size: 38px;
    font-weight: 600;
    color: #333333;
    text-align: center;
  }
  .bind-btn {
    @include horVerCenter;
    width: 678px;
    height: 94px;
    background: #ff4343;
    border-radius: 8px;
    font-size: 36px;
    font-weight: 500;
    color: #ffffff;
    margin: 73px auto 0;
  }
  .popup-item {
    display: flex;
    align-items: center;
    width: calc(100% - 72px);
    margin-left: 36px;
    font-size: 34px;
    font-weight: 500;
    height: 110px;
    padding-left: 38px;
    box-shadow: 0px 1px 0px 0px rgba(231, 231, 231, 1);
    .label {
      width: 130px;
    }
    input {
      border: 0;
    }
    img {
      width: 18px;
      height: 14px;
      margin-left: 10px;
      margin-right: 30px;
    }
    input::placeholder {
      font-size: 34px;
      color: #999999;
    }
  }
  .popup-item__code {
    @extend .popup-item;
    padding-left: 168px !important;
    input {
      width: 300px;
    }
    .phone-code {
      @include horVerCenter;
      width: 250px;
      height: 77px;
      background: #fff4f4;
      border-radius: 42px;
      font-size: 32px;
      font-weight: 500;
      color: #ff4343;
    }
  }
  .bind-topic {
    font-size: 30px;
    font-weight: 400;
    color: #ff4343;
    text-align: center;
  }

  .phone-title {
    font-size: 34px;
    font-weight: 600;
    color: #f62724;
  }
  .phone-desc {
    font-size: 26px;
    font-weight: 400;
    color: #3a3a52;
    margin-top: 5px;
  }
  .btn__bind {
    @include horVerCenter;
    width: 220px;
    height: 68px;
    background: #3a3a52;
    border-radius: 34px;
    font-size: 36px;
    font-weight: 500;
    color: #ffffff;
  }
  .bind__phone {
    @include horVerCenter;
    margin-top: 30px;
    width: 100%;
  }
}
</style>
