import { Dialog } from 'vant'

/**
 * @Date: 2020-12-29 11:23:49
 * @author: zjs
 * @description: 校验手机号
 * @param {*} phone
 * @param {*} message
 */
function isPhone (phone, message = '手机号码有误，请重填') {
  if (!phone) {
    Dialog({ message: '请填写手机号' })
    return false
  }
  if (!(/^[1]([3-9])[0-9]{9}$/.test(phone))) {
    Dialog({ message })
    return false
  }
  return true
}
export {
  isPhone
}
