<template>
  <p @click="getPhoneCode">
    {{ codeTime ? `${codeTime}s` : "获取验证码" }}
  </p>
</template>

<script>
import { isPhone } from '@/helper/reg'

export default {
  props: {
    getCodeSuccessBack: { // 验证码成功回调
      type: Function,
      default: null
    },
    getCodeErrorBack: { // 验证码失败回调
      type: Function,
      default: null
    },
    phone: { // 手机号
      type: String,
      default: ''
    },
    params: { // 发送验证码参数
      type: Object,
      default: () => ({})
    }
  },
  data () {
    return {
      codeTime: 0, // 验证码倒计时
      timerObj: 1 // 定时器对象
    }
  },
  methods: {
    /**
     * @Date: 2020-12-29 11:16:53
     * @author: zjs
     * @description: 获取手机验证码
     */
    async getPhoneCode ({ successBack = this.getCodeSuccessBack, errorBack = this.getCodeErrorBack }) {
      const { phone, codeTime } = this
      if (codeTime) return
      if (!isPhone(phone)) {
        // 失败回调
        typeof errorBack === 'function' && errorBack()
        return
      }
      try {
        this.codeTime = 60
        // 倒计时
        this.timerObj = setInterval(() => {
          this.codeTime--
          if (!this.codeTime)clearInterval(this.timerObj)
        }, 1000)
        // 获取验证码
        await this.$apis.userOperator.getPhoneCode({
          phone,
          ...this.params
        })
        // 存获取验证码时间
        localStorage.setItem('getCodeTime', new Date().getTime())
        // 成功回调
        typeof successBack === 'function' && successBack()
      } catch (error) {
        // 失败回调
        typeof errorBack === 'function' && errorBack(error)
      }
    }
  }
}
</script>
